/* ================= NAVBAR ================= */
.navbar {
  position: sticky;
  top: 10px;
  z-index: 1000;

  display: flex;
  justify-content: space-between;
  align-items: center;

  padding: 15px 30px;
  margin-bottom: 25px;

  border-radius: 14px;

  /* ðŸ”¥ Glass + Gradient */
  background: linear-gradient(135deg, #4f46e5, #7c3aed);
  backdrop-filter: blur(10px);

  color: white;

  box-shadow: 0 8px 25px rgba(0,0,0,0.15);
}

/* ================= LOGO ================= */
.logo {
  font-size: 22px;
  font-weight: 700;
  letter-spacing: 0.5px;
}

/* ================= NAV LINKS ================= */
.nav-links {
  display: flex;
  gap: 22px;
}

.nav-links a {
  text-decoration: none;
  color: rgba(255,255,255,0.9);
  font-weight: 500;

  padding: 6px 10px;
  border-radius: 6px;

  transition: all 0.3s ease;
}

/* ðŸ”¥ ACTIVE LINK */
.nav-links a.active {
  background: rgba(255,255,255,0.2);
  color: #ffd700;
}

/* Hover */
.nav-links a:hover {
  background: rgba(255,255,255,0.15);
  color: #ffd700;
}

/* ================= LOGOUT BUTTON ================= */
.logout-btn {
  background: linear-gradient(135deg, #ff4d4f, #d9363e);
  border: none;

  padding: 8px 16px;
  color: white;
  border-radius: 8px;

  cursor: pointer;
  font-weight: 500;

  transition: all 0.3s ease;
}

.logout-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 15px rgba(0,0,0,0.25);
}

/* ================= RESPONSIVE ================= */
@media (max-width: 768px) {
  .navbar {
    flex-direction: column;
    gap: 10px;
    text-align: center;
  }

  .nav-links {
    flex-wrap: wrap;
    justify-content: center;
  }
}