/* ================= BACKGROUND ================= */
.auth-container {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background: linear-gradient(135deg, #667eea, #764ba2);
}

/* ================= CARD ================= */
.auth-card {
  width: 380px;
  padding: 35px;
  border-radius: 16px;

  /* ðŸ”¥ Glass Effect */
  background: rgba(255, 255, 255, 0.15);
  backdrop-filter: blur(12px);

  box-shadow: 0 12px 30px rgba(0,0,0,0.25);
  text-align: center;
  color: white;
}

/* ================= TITLE ================= */
.auth-title {
  font-size: 24px;
  font-weight: 600;
  margin-bottom: 25px;
}

/* ================= INPUT ================= */
.auth-form input {
  width: 100%;
  margin-bottom: 15px;
  padding: 12px;

  border-radius: 8px;
  border: none;
  outline: none;

  background: rgba(255,255,255,0.2);
  color: white;
  font-size: 14px;
}

.auth-form input::placeholder {
  color: rgba(255,255,255,0.7);
}

/* ================= BUTTON ================= */
.auth-form button {
  width: 100%;
  padding: 12px;
  border-radius: 8px;

  background: linear-gradient(135deg, #00c6ff, #0072ff);
  color: white;
  border: none;
  cursor: pointer;

  font-weight: 600;
  transition: 0.3s;
}

.auth-form button:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 15px rgba(0,0,0,0.3);
}

/* ================= ERROR ================= */
.error-message {
  background: rgba(255, 0, 0, 0.15);
  color: #ffb3b3;
  padding: 8px;
  border-radius: 6px;
  margin-bottom: 10px;
}

.error-text {
  color: #ffcccc;
  font-size: 12px;
  text-align: left;
  margin-bottom: 8px;
}

/* ================= SUCCESS ================= */
.success-message {
  background: rgba(0, 255, 0, 0.15);
  color: #b6ffb6;
  padding: 8px;
  border-radius: 6px;
  margin-bottom: 10px;
}

/* ================= FOOTER ================= */
.auth-footer {
  margin-top: 18px;
  font-size: 14px;
}

.auth-footer span {
  color: #00c6ff;
  cursor: pointer;
  font-weight: bold;
}

.auth-footer span:hover {
  text-decoration: underline;
}