/* ================= CONTAINER ================= */
.admin-container {
  padding: 30px;
  background: linear-gradient(135deg, #eef2ff, #f8fafc);
  min-height: 100vh;
}

/* ================= TOP BAR ================= */
.top-bar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 25px;
}

/* ================= TITLE ================= */
.admin-title {
  font-size: 28px;
  font-weight: 700;
  color: #1e293b;
}

/* ================= REFRESH BUTTON ================= */
.refresh-btn {
  background: linear-gradient(90deg, #4f46e5, #6366f1);
  border: none;
  padding: 10px 16px;
  border-radius: 8px;
  color: white;
  font-weight: 500;
  cursor: pointer;
  transition: 0.2s;
}

.refresh-btn:hover {
  transform: scale(1.05);
}

/* ================= ERROR ================= */
.error-message {
  background: #ffe5e5;
  color: #d60000;
  padding: 10px;
  border-radius: 6px;
  margin-bottom: 15px;
}

/* ================= STATS ================= */
.stats-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 20px;
  margin-bottom: 30px;
}

.stat-card {
  background: linear-gradient(135deg, #6366f1, #8b5cf6);
  color: white;
  padding: 20px;
  border-radius: 14px;
  box-shadow: 0 6px 20px rgba(99, 102, 241, 0.3);
  text-align: center;
}

.stat-card h3 {
  margin-bottom: 10px;
  font-weight: 500;
}

.stat-card p {
  font-size: 26px;
  font-weight: bold;
}

/* ================= STORE SECTION ================= */
.store-section {
  margin-top: 20px;
}

.store-section h2 {
  margin-bottom: 15px;
  color: #1e293b;
}

/* ================= CONTROLS ================= */
.controls {
  display: flex;
  gap: 10px;
  margin-bottom: 20px;
}

.controls input {
  flex: 1;
  padding: 10px;
  border-radius: 8px;
  border: 1px solid #ddd;
}

.controls select {
  padding: 10px;
  border-radius: 8px;
  border: 1px solid #ddd;
}

/* ================= STORE GRID ================= */
.store-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(260px, 1fr));
  gap: 20px;
}

/* ================= STORE CARD ================= */
.store-card {
  background: white;
  padding: 18px;
  border-radius: 14px;
  box-shadow: 0 6px 18px rgba(0,0,0,0.08);
  transition: 0.2s ease;
  border-left: 5px solid #6366f1;
}

.store-card:hover {
  transform: translateY(-6px);
}

.store-card h3 {
  margin-bottom: 8px;
  color: #4f46e5;
}

.store-card p {
  margin: 5px 0;
  color: #555;
}

/* ================= EMPTY ================= */
.empty {
  text-align: center;
  color: #777;
}

/* ================= NAVBAR ================= */
.admin-navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: linear-gradient(90deg, #4f46e5, #6366f1);
  padding: 14px 30px;
  color: white;
}

.admin-navbar a {
  color: white;
  margin-right: 20px;
  text-decoration: none;
}

.admin-navbar .active {
  background: rgba(255,255,255,0.2);
  padding: 6px 12px;
  border-radius: 6px;
}

/* ================= LOGOUT ================= */
.logout-btn {
  background: #ef4444;
  border: none;
  padding: 8px 14px;
  border-radius: 6px;
  color: white;
  cursor: pointer;
}

.logout-btn:hover {
  background: #dc2626;
}

/* ================= RESPONSIVE ================= */
@media (max-width: 768px) {
  .stats-grid {
    grid-template-columns: 1fr;
  }

  .controls {
    flex-direction: column;
  }
}