/* ===== RESET ===== */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* ===== BODY ===== */
body {
  font-family: "Segoe UI", Roboto, Oxygen, Ubuntu, sans-serif;
  background: #f4f6f9;
  color: #333;
  line-height: 1.6;
}

/* ===== SCROLLBAR (Nice UI) ===== */
::-webkit-scrollbar {
  width: 8px;
}

::-webkit-scrollbar-thumb {
  background: #888;
  border-radius: 10px;
}

::-webkit-scrollbar-thumb:hover {
  background: #555;
}

/* ===== GLOBAL CONTAINER ===== */
.container {
  max-width: 1200px;
  margin: auto;
  padding: 20px;
}

/* ===== BUTTON ===== */
button {
  padding: 10px 16px;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  background: #4f46e5;
  color: white;
  font-weight: 500;
  transition: 0.3s;
}

button:hover {
  background: #4338ca;
}

button:disabled {
  background: gray;
  cursor: not-allowed;
}

/* ===== INPUT ===== */
input, select {
  padding: 10px;
  border-radius: 8px;
  border: 1px solid #ccc;
  outline: none;
  transition: 0.2s;
}

input:focus, select:focus {
  border-color: #4f46e5;
  box-shadow: 0 0 5px rgba(79, 70, 229, 0.3);
}

/* ===== HEADINGS ===== */
h1, h2, h3 {
  margin-bottom: 10px;
  color: #222;
}

/* ===== CARD ===== */
.card {
  background: white;
  padding: 20px;
  border-radius: 12px;
  box-shadow: 0 4px 10px rgba(0,0,0,0.1);
}

/* ===== TABLE ===== */
table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 15px;
}

th, td {
  padding: 12px;
  border-bottom: 1px solid #eee;
}

th {
  background: #f5f5f5;
  text-align: left;
}

/* ===== STATUS MESSAGES ===== */
.error-message {
  background: #ffe5e5;
  color: #d32f2f;
  padding: 10px;
  border-radius: 6px;
  margin-bottom: 10px;
}

.success-message {
  background: #e6ffed;
  color: #2e7d32;
  padding: 10px;
  border-radius: 6px;
  margin-bottom: 10px;
}

/* ===== LINKS ===== */
a {
  text-decoration: none;
  color: #4f46e5;
}

a:hover {
  text-decoration: underline;
}

/* ===== LOADING ===== */
.loading {
  text-align: center;
  padding: 20px;
  font-size: 18px;
  color: #666;
}