/* ================= CONTAINER ================= */
.user-container {
  padding: 30px;
  max-width: 1100px;
  margin: auto;
  font-family: "Segoe UI", sans-serif;
}

/* ================= HEADER ================= */
.user-container h1 {
  margin-bottom: 20px;
  color: #222;
}

/* ================= ACTIONS ================= */
.actions {
  margin-bottom: 15px;
}

.actions button {
  padding: 8px 14px;
  border: none;
  border-radius: 8px;
  background: #4f46e5;
  color: white;
  cursor: pointer;
  font-weight: 500;
}

.actions button:hover {
  background: #4338ca;
}

/* ================= SEARCH ================= */
.search-input {
  width: 100%;
  padding: 12px;
  margin: 15px 0 25px;
  border-radius: 10px;
  border: 1px solid #ddd;
  outline: none;
  transition: 0.3s;
}

.search-input:focus {
  border-color: #4f46e5;
  box-shadow: 0 0 5px rgba(79,70,229,0.3);
}

/* ================= GRID ================= */
.store-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(260px, 1fr));
  gap: 20px;
}

/* ================= CARD ================= */
.store-card {
  background: white;
  padding: 18px;
  border-radius: 14px;
  box-shadow: 0 6px 15px rgba(0,0,0,0.08);
  transition: 0.3s;
  position: relative;
}

.store-card:hover {
  transform: translateY(-6px);
}

/* Store Name */
.store-card h3 {
  margin-bottom: 8px;
  color: #333;
}

/* Address */
.store-card p {
  margin: 4px 0;
  color: #555;
}

/* ================= BADGES ================= */
.store-card .avg {
  font-weight: 600;
  color: #4f46e5;
}

.store-card .my {
  font-weight: 600;
  color: #16a34a;
}

/* ================= STARS ================= */
.star-row {
  margin-top: 12px;
}

.star {
  font-size: 22px;
  cursor: pointer;
  color: #ddd;
  margin-right: 6px;
  transition: 0.2s;
}

/* Hover effect */
.star:hover {
  transform: scale(1.2);
  color: #f4c150;
}

/* Active */
.star.active {
  color: #f4c150;
}

/* Disabled (while loading) */
.star.disabled {
  cursor: not-allowed;
  opacity: 0.5;
}

/* ================= STATUS ================= */
.error-message {
  background: #ffe5e5;
  color: #d32f2f;
  padding: 10px;
  border-radius: 8px;
  margin-bottom: 15px;
}

.success-message {
  background: #e6ffed;
  color: #2e7d32;
  padding: 10px;
  border-radius: 8px;
  margin-bottom: 15px;
}

/* ================= LOADING ================= */
.rating-loading {
  font-size: 12px;
  color: #666;
  margin-top: 5px;
}

.loading {
  text-align: center;
  margin-top: 30px;
  font-size: 16px;
  color: #555;
}

.empty {
  text-align: center;
  color: #888;
  margin-top: 20px;
  font-style: italic;
}

/* ================= RESPONSIVE ================= */
@media (max-width: 768px) {
  .user-container {
    padding: 15px;
  }

  .store-card {
    padding: 15px;
  }
}

.user-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.logout-btn {
  background: #ff4d4f;
  border: none;
  padding: 8px 14px;
  color: white;
  border-radius: 6px;
  cursor: pointer;
  margin-left: 10px;
}

.logout-btn:hover {
  background: #d9363e;
}