/* ================= CONTAINER ================= */
.store-container {
  padding: 30px;
  background: #f4f6f9;
  min-height: 100vh;
  font-family: "Segoe UI", sans-serif;
}

/* ================= TITLE ================= */
.store-title {
  font-size: 28px;
  font-weight: 700;
  margin-bottom: 25px;
  color: #222;
}

/* ================= ACTION BAR ================= */
.actions {
  display: flex;
  gap: 10px;
  margin-bottom: 20px;
}

.actions button,
.actions select {
  padding: 8px 14px;
  border-radius: 8px;
  border: none;
  cursor: pointer;
  font-weight: 500;
}

.actions button {
  background: #4f46e5;
  color: white;
}

.actions button:hover {
  background: #4338ca;
}

.actions select {
  background: white;
  border: 1px solid #ddd;
}

/* ================= STATS ================= */
.stats-row {
  display: flex;
  gap: 20px;
  margin-bottom: 25px;
}

/* ================= AVG CARD ================= */
.avg-card {
  background: linear-gradient(135deg, #4facfe, #00f2fe);
  padding: 25px;
  border-radius: 14px;
  color: white;
  width: 260px;
  text-align: center;

  box-shadow: 0 8px 20px rgba(0,0,0,0.2);
  transition: 0.3s;
}

.avg-card:hover {
  transform: translateY(-4px);
}

.avg-card h3 {
  margin-bottom: 10px;
}

.avg-number {
  font-size: 36px;
  font-weight: bold;
}

/* ================= SECTION ================= */
.ratings-section {
  background: white;
  padding: 20px;
  border-radius: 14px;
  box-shadow: 0 6px 20px rgba(0,0,0,0.08);
}

/* ================= TABLE ================= */
.ratings-table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 15px;
}

.ratings-table th,
.ratings-table td {
  padding: 12px;
  border-bottom: 1px solid #eee;
  text-align: left;
}

.ratings-table th {
  background: #f5f6fa;
  font-weight: 600;
}

.ratings-table tr {
  transition: 0.2s;
}

.ratings-table tr:hover {
  background: #f9faff;
}

/* ================= STARS ================= */
.star {
  font-size: 18px;
  color: #ddd;
}

.star.filled {
  color: #f4c150;
}

/* ================= STATES ================= */
.loading {
  text-align: center;
  margin-top: 100px;
  font-size: 18px;
  color: #555;
}

.empty {
  text-align: center;
  color: #777;
  margin-top: 20px;
  font-style: italic;
}

/* ================= ERROR ================= */
.error-message {
  background: #ffe5e5;
  color: #d32f2f;
  padding: 10px;
  border-radius: 8px;
  margin-bottom: 15px;
}

/* ================= RESPONSIVE ================= */
@media (max-width: 768px) {
  .stats-row {
    flex-direction: column;
  }

  .avg-card {
    width: 100%;
  }

  .actions {
    flex-direction: column;
  }
}

.store-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.actions {
  display: flex;
  gap: 10px;
}

.logout-btn {
  background: #ff4d4f;
  border: none;
  padding: 8px 16px;
  color: white;
  border-radius: 8px;
  cursor: pointer;
}

.logout-btn:hover {
  background: #d9363e;
}